<template>
  <div id="detail">
    <de-nav-bar @titleClick="titleClick"  :currentIndex= "currentIndex"/>
    <scroll class="de-scroll" ref="scrolltop" :probetype="3" @cscroll="contentScroll">
      <de-swiper :swiperimage="swiperitem"></de-swiper>
      <detail-base-info :baseinfo="goodsbaseInfo"></detail-base-info>
      <detail-shop-info :shopinfo="shopInfo" ref="recommend"></detail-shop-info>
      <detail-more-info :goodsinfo="goodsdetailInfo" @imgload="moreimgload" ref="comment"></detail-more-info>
      <detail-params-info :paramsInfo="paramInfo" ref="params"></detail-params-info>
      <detail-comment :comment-info="commentInfo"></detail-comment>
      <detail-recommend :recommend-info="recommendInfo"></detail-recommend>
    </scroll>
    <detail-bottom-bar @addToCart="addToCart"></detail-bottom-bar>
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>

  </div>
</template>

<script>
import {
  getDetailData,
  Goods,
  Shop,
  ParamsInfo,
  getRecommend
} from "../../network/detail";
import { imgloadMix ,backTopMix} from "common/mixsin.js";
import { debounce } from "common/utils.js";

import Scroll from "components/common/betterscroll/BetterScroll";

import DeNavBar from "./childComps/DetailNavBar";
import DeSwiper from "./childComps/DetailSwiper";
import DetailBaseInfo from "./childComps/DetailBaseInfo";
import DetailShopInfo from "./childComps/DetailShopInfo";
import DetailMoreInfo from "./childComps/DetailMoreInfo";
import DetailParamsInfo from "./childComps/DetailParamsInfo";
import DetailRecommend from "./childComps/DetailRecommand";
import DetailComment from "./childComps/DetailComment"
import DetailBottomBar from "./childComps/DetailBottomBar"

// import Toast from '../../components/common/toast'

  // Vue.use(Toast)

export default {
  name: "Detial",
  components: {
    // navBar,
    DeNavBar,
    DeSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailMoreInfo,
    DetailParamsInfo,
    DetailRecommend,
    DetailComment,
    DetailBottomBar,
    Scroll
  },
  data() {
    return {
      iid: null,
      swiperitem: [],
      goodsbaseInfo: {},
      shopInfo: {},
      goodsdetailInfo: {},
      commentInfo: {},
      paramInfo: {},
      recommendInfo: [],
      themeTopYs: [0, 100, 1000, 2000],
      getThemeTopY: null,
      currentIndex: 0,
      message: ''
    };
  },
  mixins: [imgloadMix,backTopMix],
  created() {
    // 1. 保存传入的iid

    this.iid = this.$route.params.iid;

    getDetailData(this.iid).then(res => {
      console.log(res);
      // 1. 获取轮播图图片
      this.swiperitem = res.result.itemInfo.topImages;

      // 2. 获取商品基础信息
      this.goodsbaseInfo = new Goods(
        res.result.itemInfo,
        res.result.columns,
        res.result.shopInfo.services
      );
      // 3. 获取商店信息
      this.shopInfo = new Shop(res.result.shopInfo);
      // 4. 获取商品详细信息
      this.goodsdetailInfo = res.result.detailInfo;
      //5. 保存参数信息
      this.paramInfo = new ParamsInfo(
        res.result.itemParams.info,
        res.result.itemParams.rule
      );
      //6. 保存评论信息
      this.commentInfo = res.result.rate;
    });

    getRecommend().then((res, err) => {
      if (err) return;
      console.log(res);
      this.recommendInfo = res.data.list;
    });

    // 1. 第一次提取，值不对，是因为this.$refs.params.$el根本没有渲染
    // this.themeTopYs = [];
    // this.themeTopYs.push(0);
    // this.themeTopYs.push(this.$refs.params.$el.offsetTop);
    // this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
    // this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);

    // this.$nextTick(() => {
    //   // 2. 第二次提取，值不对
    //   // 原因：图片没有被计算在内
    //   // 根据最新的数据，对应的DOM是已经被渲染出来 但是图片依然没有加载完成
    //   this.themeTopYs = [];
    //   this.themeTopYs.push(0);
    //   this.themeTopYs.push(this.$refs.params.$el.offsetTop);
    //   this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
    //   this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
    // });
    // 4. 给getThemeTopY赋值（对给this.themeTopYs赋值的操作进行防抖）
    this.getThemeTopY = debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.params.$el.offsetTop);
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      this.themeTopYs.push(Number.MAX_VALUE)
      // console.log(this.themeTopYs);
    });
  },
  mounted() {
    // this.$bus.$on("imgload");
  },
  methods: {

    // 监听详情图片加载完成情况
    moreimgload() {
      this.newRefresh();
      // let refresh = debounce(this.$refs.scrolltop.refresh,100)
      // this.getThemeTopY()
    },
    contentScroll(position) {
      const positionY = -position.y;
      // console.log(positionY);
      this.lisenterTheme(positionY)

      //显示backtOp
      this.isShowBackTop = positionY >1000
    },

    // 监听滚动位置的变化，到合理的位置调整顶部导航栏的选中效果
    lisenterTheme(position) {

      let length  = this.themeTopYs.length;
      for(let i = 0 ; i<length ;i++) {
        if( this.currentIndex !== i &&(position > this.themeTopYs[i] && position <this.themeTopYs[i+1])) {
            this.currentIndex = i;
        }
      }
    },
    addToCart() {
      // 1. 获取购物车中需要展示的数据
      let prodcuct = {}
      prodcuct.image = this.swiperitem[0];
      prodcuct.title = this.goodsbaseInfo.title;
      prodcuct.desc = this.goodsbaseInfo.desc;
      prodcuct.price =this.goodsbaseInfo.oldPrice;
      prodcuct.iid = this.iid;
      prodcuct.countprice = parseFloat(this.goodsbaseInfo.realPrice);

      
      
      //2. 将商品添加到购物车里
      this.$store.dispatch('addToCart',prodcuct).then(res => {
        console.log(res);
        // this.message = res
        console.log(this.$toast);
        
        this.$toast.show(res,1500)
      })
      
      
      //3. 返回添加状态
      
    }
    ,
    titleClick(index) {
      this.currentIndex = index
      this.$refs.scrolltop.ScrollTo(0, -this.themeTopYs[index], 200);
    }
  },
  disactived() {
    this.$bus.$off("itemimageLoad", this.imgListener);
  }
};
</script>

<style scoped>
#detail {
  position: relative;
  height: 100vh;
  background-color: #fff;
  z-index: 10;
  /* background-color: #; */
}

.de-scroll {
  position: absolute;
  background-color: #fff;
  top: 44px;
  bottom: 58px;
  z-index: 9;
  overflow: hidden;
}
</style>