<template>
  <div class="shop-info">
    <div class="shop-top">
      <img :src="shopinfo.shopLogo" alt />
      <span>{{shopinfo.name}}</span>
    </div>
    <div class="shop-middle">
      <div class="shop-middle-item shop-middle-left">
        <div class="all-sell">
          <div class="sells-count">{{shopinfo.cSells | sellCountFliter }}</div>
          <div class="sells-text">
            <span>总销量</span>
          </div>
        </div>
        <div class="goods-info">
          <div class="goods-count">{{shopinfo.cGoods}}</div>
          <div class="goods-text">
            <span>全部宝贝</span>
          </div>
        </div>
      </div>
      <div class="shop-middle-item shop-middle-right">
        <table>
          <tr v-for="(item,index) in shopinfo.score" :key="index">
            <td>{{item.name}}</td>
            <td class="score" :class="{'score-better': item.isBetter}">{{item.score}}</td>
            <td class="better" :class="{'better-more': item.isBetter}">
              <span>{{item.isBetter ? '高':'低'}}</span>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="shop-bottoms">
      <div class="shop-button">进店逛逛</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShopInfo",
  props: {
    shopinfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  filters: {
    sellCountFliter: function(value) {
      if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    }
  }
};
</script>

<style scoped>
.shop-middle {
  padding: 0 6px 0 6px;
  display: flex;
  align-items: center;
}

.shop-middle-items {
  float: 1;
}

.shop-middle-left {
  display: flex;
  justify-content: space-evenly;
  color: #333;
  text-align: center;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.shop-middle-left .all-sell,
.goods-info {
  padding: 0px 17px 0px 17px;
}

.sells-count,
.goods-count {
  font-size: 19px;
  line-height: 30px;
}

.sells-text,
.goods-text {
  font-size: 15px;
}

.shop-middle-right {
  font-size: 13px;
  color: #333;
}

.shop-middle-right table {
  width: 120px;

  margin-left: 30px;
}

.shop-middle-right td {
  padding: 5px 0;
}

.shop-middle-right .score {
  color: #5ea732;
}

.shop-middle-right .score-better {
  color: red;
}

.shop-middle-right .better span {
  background-color: #5ea732;
  color: #fff;
  text-align: center;
}

.shop-middle-right .better-more span {
  background-color: red;
}

.shop-top {
  line-height: 80px;
  padding: 0 6px 0 6px;
  font-size: 20px;
  color: #999;
}

.shop-top img {
  height: 40px;
  width: 40px;
  margin-right: 10px;
  border-radius: 50%;
  vertical-align: middle;
}

.shop-bottoms {
  height: 40px;
  /* line-height: 40px; */
  /* border-right: 1px solid rgba(0, 0, 0, 0.1); */
  border-bottom: 3px solid rgba(100, 100, 100, 0.1);

  text-align: center;
  margin-bottom: 15px;
}

.shop-bottoms .shop-button {
  display: inline-block;
  height: 20px;
  width: 150px;
  background-color: #aaa;
  margin-top: 10px;
  line-height: 20px;
  font-size: 14px;
  border-radius: 5px;
}
</style>