<template>
  <div v-if="Object.keys(goodsinfo).length !== 0 " class="detail-more-info">
    <div class="text-desc clear-fix">
      <div class="start"></div>
      <div class="text">{{goodsinfo.desc}}</div>
      <div class="end"></div>
    </div>
    <div class="img-desc">
      <div class="desc-key">{{goodsinfo.detailImage[0].key}}</div>
      <div class="img-list">
        <img
          v-for="(item,index) in goodsinfo.detailImage[0].list"
          :key="index"
          :src="item"
          @load="imgload"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailMoreInfo",
  props: {
    goodsinfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  methods: {  
    imgload() {
      this.$emit("imgload");
    }
  }
};
</script>

<style scoped>
.detail-more-info {
  padding: 20px 0;
  border-bottom: 5px solid #f2f5f8;
}
.text-desc {
  padding: 0px 10px;
}

.text-desc .start {
  float: left;
}

.text-desc .end {
  float: right;
}

.text-desc .start::before,
.text-desc .end::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #333;
  bottom: 0;
}

.text-desc .end::after {
  right: 0;
}

.text-desc .text {
  padding: 15px 0;
  font-size: 14px;
}

.img-desc .img-list img {
  width: 100%;
}

.img-desc .desc-key {
  padding-left: 10px;
  font-size: 15px;
  color: black;
}
</style>