<template>
  <div class="comment-info">
    <div class="comment-title">
      <div class="title flex">用户评价</div>
      <div class="more flex">更多</div>
    </div>
    <div class="comment-content">
      <div class="user">
        <img :src="commentInfo.list[0].user.avatar" alt />
        <span>{{commentInfo.list[0].user.uname}}</span>
      </div>
      <div class="text">{{commentInfo.list[0].content}}</div>
      <div class="other">
          <span class="time">{{commentInfo.list[0].created | showData }}</span>
          <span class="style">{{commentInfo.list[0].style}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import {formatDate} from "../../../common/utils.js"

export default {
  name: "DetailComment",
  props: {
    commentInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  filters: {
    showData(value) {
        let date = new Date(value *1000)
        return formatDate(date,'yyyy-MM-dd hh:mm:ss')
    }  
  },
  mounted() {
    console.log(this.commentInfo);
  }
};
</script>

<style scoped>
.comment-info {
  border-bottom: 3px solid rgba(100, 100, 100, 0.1);
}

.comment-title {
  display: flex;
  /* height: 40px; */
  padding: 0 6px;
  line-height: 50px;
  border-bottom: 2px solid rgba(100, 100, 100, 0.1);
  justify-content: space-between;
}
.comment-title .title {
  font-size: 18px;
}

.comment-title .more {
  font-size: 14px;
  color: black;
}
</style>