<template>
  <nav-bar >
      <div slot = "left" @click="backtohome">
          <img src="~assets/img/common/back.svg" alt="">
      </div>
      <div slot = "center" class = "de-nav-bar">
          <div v-for="(item,index) in baritem" :key="index" class="bar-item" @click="titleClick(index)" :class="{select: index===currentIndex}">
              {{item}}
          </div>
      </div>
  </nav-bar>
</template>

<script>

import navBar from  'components/common/navbar/NavBar'


export default {
    name:'DeNavBar',
    props: {
        currentIndex: {
            type: Number,
            default() {
                return 0
            }
        }
    },
    data () {
        return {
            baritem: ['商品','参数','评论','推荐']      ,
            // clickIndex: 0
        }
    },
    nents: {
        navBar
    },
    methods: {
        backtohome() {
            this.$router.go(-1)
        },
        titleClick(index) {
            this.$emit("titleClick",index)
            this.currentIndex = index
        }
    }
}
</script>

<style>
.de-nav-bar{
    display: flex;
    text-align: center;
    font-size: 15px;
}
.bar-item {
    flex: 1;
}

.select {
    color: pink;
}

</style>